import React from 'react'
import { IconButton, Card, CardContent, Typography, CardActions, CardActionArea, Grid, } from '@material-ui/core'
import DeleteIcon from '@material-ui/icons/Delete';
import { makeStyles } from '@material-ui/core/styles';
import DoneIcon from '@material-ui/icons/Done';
import ClearIcon from '@material-ui/icons/Clear';

import { db } from '../firebase';
import Masonry from 'react-masonry-css';
import './style.css'
const styles = makeStyles(theme => ({
    wrap: {
        wordWrap: "break-word",
        padding: 10,
    },
    red: {
        border: "1px solid red"
    },

}))

const beakpoints = {
    default: 4,
    1500: 3,
    1100: 3,
    700: 2,
    500: 1
}

function RenderTodos({ data, state }) {
    const [, setS] = state;
    const classes = styles();
    const updateChange = (id, value) => {
        db.collection("/todos").doc(id).update({
            status: !value,
        })
        .then(r => console.log('object updated'))
        .catch(e=>console.log(e))
    }
    function deleteCurrent(id) {
        db.collection('todos').doc(id).delete()
        .then(r => console.log('object deleted'))
        .catch(e=>console.log(e))
    }
    return (
        // data[0]?.d?.title
        <div >
            <Masonry
                breakpointCols={beakpoints}
                className="my-masonry-grid"
                columnClassName="my-masonry-grid_column"
            >
                {/* <Grid container spacing="2"> */}
                {
                    data?.map(cd => {
                        return (
                            <div key={cd.id} >
                                <Card className={cd?.d?.status === false ? classes.red : ""} variant="outlined">
                                    <CardActionArea onClick={() => setS({ ...cd?.d, id: cd?.id })}>
                                        <CardContent>
                                            <Typography gutterBottom variant="h5" component="h2">
                                                {cd?.d?.title}
                                            </Typography>
                                            <Typography className={classes.wrap} variant="body2" color="textSecondary" component="p">
                                                {cd?.d?.description}
                                            </Typography>
                                        </CardContent>

                                    </CardActionArea>
                                    <CardActions>
                                        <Grid container justifyContent="flex-end">
                                            <Grid item >
                                                <span onClick={() => updateChange(cd?.id, cd?.d?.status)}>
                                                    <IconButton color="primary" aria-label="edit">
                                                        {cd?.d?.status === true ? <ClearIcon /> : <DoneIcon />}
                                                    </IconButton>
                                                </span>
                                            </Grid>
                                            <Grid item>
                                                <span onClick={() => deleteCurrent(cd?.id)}>
                                                    <IconButton color="secondary" aria-label="delete" >
                                                        <DeleteIcon />
                                                    </IconButton>
                                                </span>
                                            </Grid>
                                        </Grid>
                                    </CardActions>
                                </Card>
                            </div>
                        )
                    })
                }

                {/* </Grid> */}

            </Masonry>
        </div>
    )
}

export default RenderTodos
