import React, { useState } from 'react'
import { TextField, Select, MenuItem, Button, Grid, ButtonGroup, Divider } from '@material-ui/core'
import { createTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import { db } from '../firebase';
import { useEffect } from 'react';


const theme = createTheme({
    palette: {
        primary: {
            main: '#2EFF2E'
        }
    }
})

function AddTodo({ initialData }) {

    const [d,] = initialData
    const [name, setName] = useState('');
    const [description, setdescription] = useState('');
    const [status, setStatus] = useState("uncompleate");
    const [id, setId] = useState('');

    useEffect(() => {
        // console.log(d);
        setName(d?.title)
        setdescription(d?.description)
        setId(d?.id);
        if (d?.status) {
            setStatus("compleate")
        } else {
            setStatus("uncompleate")
        }
    }, [d])
    const resetAll = e => {
        setName('')
        setdescription('')
        setStatus("uncompleate")
        setId('')
    }

    const submit = (e) => {
        e.preventDefault();

        let ans = false;
        if (status === 'compleate')
            ans = true;

        if (!id) {
            db.collection('todos').add({
                title: name,
                description: description,
                status: ans,
                timestamp: Date.now()
            }).then(r => console.log('created'))
        } else {
            db.collection('todos').doc(id).update({
                title: name,
                description: description,
                status: ans,
                timestamp: Date.now()
            }).then(r => console.log('updated'))
        }
    }
    return (

        <form onSubmit={submit} onReset={resetAll} style={{ marginTop: '40px'}}>
            <Grid container spacing={3} justifyContent="space-evenly" alignItems="center">
                <Grid item  >
                    <TextField value={name} name="title" onChange={e => setName(e.target.value)} label="Title" variant="outlined" />    
                </Grid>
                <Grid item  >
                    <TextField value={description} name="description" onChange={e => setdescription(e.target.value)} label="description" variant="outlined" />
                </Grid>
                <Grid item  >
                    <Select
                        value={status}
                        onChange={e => setStatus(e.target.value)}
                    >

                        <MenuItem value={"compleate"}>Compleate</MenuItem>
                        <MenuItem value={"uncompleate"}>Not Compleate</MenuItem>
                    </Select>
                </Grid>
                <Grid item  >
                <ThemeProvider theme={theme}>
                    <ButtonGroup>
                        <Button variant="contained" color="primary" type="submit"> {id?"Update":"Add"} </Button>
                        <Button variant="contained" color="secondary" type="reset" > Reset </Button>
                    </ButtonGroup>
                </ThemeProvider>
                </Grid>
            </Grid>
            <br /><br />
            <Divider/>
        </form>

    )
}

export default AddTodo
