import React, { useEffect, useState } from 'react'
import RenderTodos from './RenderTodos'
import LoadingCardSkeleton from './LoadingCardSkeleton';
import { db } from '../firebase';
import AddTodo from './AddTodoForm';

function Home() {
  
  const [data, setData] = useState([]);

  const [loading, setLoading] = useState(true);

  useEffect(() => {
    
    db.collection('todos').orderBy("timestamp",'desc').onSnapshot(snapshot => {
      const items = [];
      snapshot.forEach(doc => items.push({ d: doc.data(), id: doc.id }))
      setData(items);
      setLoading(false);
    })

  }, [])

  const currentEdit = useState({ title: '', description: '', status:false, id:""});

  return (
    <div style={{overflow:'hidden'}}>
      <AddTodo initialData={currentEdit}/>
      <br />
      <br />
      {
        loading?<LoadingCardSkeleton />:<RenderTodos data={data} state={currentEdit} />
      }
    </div>
  )
}

export default Home
